.todoList {
    margin-top: 20px;

    input {
        padding: 5px;
    }

    .input {
        display: flex;
        justify-content: center;
        height: 30px;
    }

    .listBody {
        display: flex;
        justify-content: space-around;
        font-size: 20px;
        .listItems {
            width: 30%;
            .listItem {
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                .listItemBtm{
                    margin-right: 10px;
                }
            }
        }
    }
}